<template>
  <div id="back">
  <div id="top" class="">
    <div id="title-back"></div><h1 class="m-0"></h1>
    <img :src="'/img/carousel_04.jpg'">
  </div>
  <div class="container">
    <div class=" mb-3 mt-3">
      <h2 class="text-center mb-4 mt-4"> </h2>
    </div>
    <form @submit="submit" class="mb-5">
      <b-form-file
          v-model="file"
          :state="state"
          placeholder=""
          drop-placeholder=""
          required
          browse-text=""
          accept="image/*"
          @input="validateFile">
      </b-form-file>
      <p class="text-danger"> </p>
      <!--  -->
    <div class="sort mb-3">
      <b-form-select
      v-model="selected"
      :options="options"
      placeholder=""
      ></b-form-select>
    </div>
    <b-form-textarea
      v-model="description"
      placeholder=""
      rows="3" max-rows="6"
      maxlength="200"
      @input="validateText"
      :state="textstate">
    </b-form-textarea>
    <b-button class="mt-3"  type="submit" variant="primary"></b-button>
    </form>
  <div>
  <b-card no-body class="mb-3">
    <b-tabs card v-model="activetab">
      <b-tab no-body :title="t" class="p-3" v-for="(t, index) of tabs" :key="index">
        <b-row >
          <b-col class="col-12 col-md-6 col-lg-3 mb-4" style="" v-for="(image, idx) in Pagination" :key='idx'>
            <b-card class="">
              <div class="img-size">
                <img :src="image.src" class="img-back"></div>
                  <b-card-body>
                    <b-btn class="mb-2" v-if="image.edit" variant="danger" @click="cancel(image)"></b-btn>
                    <b-btn class="mb-2" v-else variant="light" @click="edit(image)"></b-btn>
                    <b-btn v-if="image.edit" variant="info" @click="update(image)"></b-btn>
                    <b-btn c v-else variant="danger" @click="del(image)"></b-btn>
                    <hr>
                    <!--  -->
                    <h6 class="text-center mb-3"> {{ image.type }} </h6>
                    <hr>
                    <pre v-if="!image.edit"> {{ image.text }} </pre>
                    <b-form-textarea v-else v-model="image.model"></b-form-textarea>
                  </b-card-body>
            </b-card>
          </b-col>
        </b-row>
    </b-tab>
        </b-tabs>
      </b-card>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'work',
  data () {
    return {
      images: [],
      file: null,
      text: '',
      description: '',
      state: null,
      textstate: null,
      selected: null,
      options: [
        { value: null, text: '', disabled: true },
        { value: '', text: '' },
        { value: '', text: '' },
        { value: '', text: '' }
      ],
      tabs: [
        '',
        '',
        ''
      ],
      activetab: 0
    }
  },
  methods: {
    validateFile () {
      if (this.file != null) {
        if (this.file.size >= 5209 * 3648 || !this.file.type.includes('image')) {
          this.state = false
          this.file = null
        } else {
          this.state = true
        }
      }
    },
    validateText () {
      if (this.description.length > 200) {
        this.textstate = false
      }
      // this.textstate = (!this.description.length > 200)
    },
    submit (event) {
      event.preventDefault()
      if (this.file === null || this.file.size >= 5209 * 3648 || !this.file.type.includes('image')) {
        alert('')
      } else {
        // FormData 
        const fd = new FormData()
        fd.append('image', this.file)
        fd.append('description', this.description)
        fd.append('type', this.selected)
        this.axios.post(process.env.VUE_APP_APIURL + '/file', fd, {
          //  axios  json formdata
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        }).then(response => {
          this.images.push(
            {
              type: this.selected,
              text: this.description,
              src: process.env.VUE_APP_APIURL + '/file/' + response.data.name,
              _id: response.data._id,
              edit: false,
              model: response.data.name
            }
          )
          this.file = null
          this.description = ''
          this.selected = null
        }).catch(error => {
          alert(error.response.data.message)
        })
      }
    },
    edit (image) {
      image.edit = true
      image.model = image.text
    },
    update (image) {
      this.axios.patch(process.env.VUE_APP_APIURL + '/file/' + image._id, { description: image.model })
        .then(response => {
          image.edit = false
          image.text = image.model
        })
        .catch(() => {
          alert('')
        })
    },
    cancel (image) {
      image.edit = false
      image.model = image.text
    },
    // 
    del (image) {
      this.axios.delete(process.env.VUE_APP_APIURL + '/file/' + image._id)
        .then(response => {
          const idx = this.images.findIndex(i => {
            return i === image
          })
          this.images.splice(idx, 1)
        })
        .catch(() => {
          alert('')
        })
    }
  },
  computed: {
    user () {
      return this.$store.getters.user
    },
    // this.image = (image => image.type === this.tabs[this.activetab]) .slice(this.currentPage - 1)
    Pagination () {
      return this.images.filter(image => image.type === this.tabs[this.activetab]).slice(this.currentPage - 1)
    }
  },
  mounted () {
    this.axios.get(process.env.VUE_APP_APIURL + '/files')
      .then(response => {
        this.images = response.data.result.map(d => {
          return {
            type: d.type,
            text: d.description,
            src: process.env.VUE_APP_APIURL + '/file/' + d.name,
            _id: d._id,
            edit: false,
            model: d.name
          }
        })
      })
      .catch(() => {
        alert('')
      })
  }
}
</script>
